import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Index',
    component: () => import('../pages/Index.vue')
  },
  {
    path: '/product',
    name: 'Product',
    component: () => import('../pages/Product.vue'),
    children: [
      {
        path: 'automation',
        name: 'Automation',
        component: () => import('../pages/Automation.vue')
      },
      {
        path: 'hydropower',
        name: 'Hydropower',
        component: () => import('../pages/Hydropower.vue')
      },
      {
        path: 'vibrationTransport',
        name: 'VibrationTransport',
        component: () => import('../pages/VibrationTransport.vue')
      },
    ]
  },
  {
    path: '/contact',
    name: 'Contact',
    component: () => import('../pages/Contact.vue')
  }
]

const router = new VueRouter({
  routes
})

export default router